import { React, useRef } from 'react'
import '../App.css';
import tony from '../assets/images/tony-stark.png'
import ironman from '../assets/images/iron-man-suit.png'
import ParticleBackground from '../components/particles';
import { useNavigate } from "react-router-dom";

function Home() {

  const navigate = useNavigate();
  const division = useRef();

  const scrollToSection = (elementRef) => {
    window.scrollTo({
      top: elementRef.current.offsetTop,
      behavior: 'smooth'
    })
  }


  function gotopersonal() {
    window.scrollTo({ top: 0, left: 0 })
    var delayInMilliseconds = 300;

    setTimeout(function () {
      navigate('/personal')
    }, delayInMilliseconds);
    
  }

  function gotoprofessional() {
    window.scrollTo({ top: 0, left: 0 })
    var delayInMilliseconds = 300;

    setTimeout(function () {
      navigate('/professional')
    }, delayInMilliseconds);
  }

  return (
    <div className='App'>
      <ParticleBackground />
      <div className='wrap'>

        <div className='blur-text'>
          <h3 className='intro-name'>Hi, I am Jeeva Manoj</h3>
          <h3 className='welcome-note'>Welcome to the Palette of My Imagination&nbsp;&nbsp;&nbsp;&nbsp;<span className='started-span'><a className='get-started' onClick={() => { scrollToSection(division) }}>Get Started</a></span></h3>

        </div>
      </div>
      <div id='divide' ref={division} className='wrap'>

        <div className='row div-wrap'>
          <div className='col-md-5 divisions' onClick={gotopersonal}>
            <img src={tony} className='images'></img>
            <h3 className='division-text'>Personal</h3>
          </div>
          <div className='col-md-5 divisions' onClick={gotoprofessional}>
            <img src={ironman} className='images1'></img>
            <h3 className='division-text'>Professional</h3>
          </div>
        </div>

      </div>
    </div>
  )
}

export default Home